.navbar{
	position: fixed !important;
	width:100%;
	background: rgb(50, 50, 50);
	height: 56px;
	top: 0;
	left: 0;
}

.navbar-navigation{
	display: flex;
	align-items: center;
	padding: 0 1em;
	height:100%;
}

.navbar-logo{
	margin: 5px;
}

.navbar-logo a{
	color: white;
	text-decoration: none;
	font-size: 1.5em;
}

.spacer{
	flex: 1;
}

.navbar-nav-items ul{
	list-style: none;
	margin: 0;
	padding: 0;
	display:flex;
}

.navbar-nav-items li{
	padding: 0 .5em;
}


.navbar-nav-items a{
	color: white;
	text-decoration:none;
	
}

.navbar-nav-items a:hover,.navbar-nav-items a:active{
	color: grey;
}

.navbar-collapse{
	margin: 10px;
}